@import url("https://fonts.googleapis.com/css2?family=Trocchi&display=swap");
@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@500&family=JetBrains+Mono:wght@500&display=swap");

body {
	margin: 0;
	height: 100%;
}

html {
	height: 100%;
}

#root {
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;

	--main-font: "Trocchi", serif;
	--mono-font: "JetBrains Mono", monospace;

	/* default hsl(6, 46, 42) */
	--start-hue: 6;
	--saturation: 30%;
	--lightness: 40%;

	--primary-color1: hsl(var(--start-hue), var(--saturation), var(--lightness));
	--primary-color2: hsl(calc(var(--start-hue) + 90), var(--saturation), var(--lightness));
	--primary-color3: hsl(calc(var(--start-hue) + 270), var(--saturation), var(--lightness));
	--primary-color4: hsl(calc(var(--start-hue) + 180), var(--saturation), var(--lightness));
	--primary-color5: hsl(54,  var(--saturation), var(--lightness));

	/* --primary-color1: rgb(156, 67, 58);
	--primary-color2: rgb(98, 156, 58);
	--primary-color3: rgb(116, 58, 156);
	--primary-color4: rgb(58, 147, 156);
	--primary-color5: rgb(179, 168, 72); */

	--joker-color1: chartreuse;
	--joker-color2: tomato;
	--joker-color3: purple;

	--grey-lighter: rgb(230, 230, 230);
	--grey-light: rgb(225, 225, 225);
	--grey: rgb(211, 211, 211);
	--grey-dark: rgb(169, 169, 169);
	--grey-darker: rgb(128, 128, 128);
	--grey-reddish: rgb(138, 125, 125);
	--black: rgb(0, 0, 0);

	--header-color: rgb(174, 103, 91);
	--button-highlight: rgb(240, 230, 140); /* khaki */
	--button-effect: rgb(173, 255, 47); /* greenyellow */

	--bg-color: rgba(173, 167, 144, 0.7);
	--textbox-bg: var(--grey-light);
	--g-button-bg: var(--grey);
	--g-button-hover-bg: rgb(240, 230, 140);
	--clear-click-bg: rgb(173, 255, 47);

	--discourse-display-bg: var(--grey);

	--discourse-1-title-bg: var(--primary-color1);
	--discourse-2-title-bg: var(--primary-color2);
	--discourse-3-title-bg: var(--primary-color3);
	--discourse-4-title-bg: var(--primary-color4);
	--outer-ring-title-bg: var(--primary-color5);

	--agent: var(--primary-color1);
	--other: var(--primary-color2);
	--product: var(--primary-color3);
	--truth: var(--primary-color4);
	--matheme-option-bg: var(--grey-lighter);

	--nametable-bg: var(--grey-reddish);
	--nametable-title-bg: var(--header-color);
	--nametable-header-bg: var(--grey-dark);
	--new-discourse-option-bg: var(--grey-lighter);

	--overview-title-bg: var(--header-color);
	--overview-body-bg: var(--grey-dark);
	--ring-even-bg: var(--grey);
	--ring-odd-bg: var(--grey);
	--ring-title-text: var(--black);
	--ring-title-bg: var(--grey-dark);
	--ring-title-even-bg: var(--grey-dark);
	--in-ring-name-text: var(--black);

	--info-box-outer-bg: var(--grey);
	--info-box-inner-bg: var(--grey-light);

	--blaze-color: var(--joker-color1);
	--blaze-shadow: var(--joker-color2);
	--four20-color: var(--joker-color2);
	--four20-shadow: var(--joker-color3);

	--box-shadow: 1px 1px rgb(0, 0, 0, 0.4), 2px 2px rgb(0, 0, 0, 0.3),
		4px 4px rgba(0, 0, 0, 0.2);

	font-family: var(--main-font);
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

.image-container img {
	max-height: 100%;
	width: auto;
}
