.graph-settings-box {
	grid-column-start: 1;
	grid-column-end: 2;
	grid-row-start: 1;
	grid-row-end: 3;

	width: max-content;
	height: max-content;
	padding: 2%;
	margin-bottom: -12px;
	margin-right: -35px;

    align-self: flex-end;
    justify-self: flex-end;

	background: var(--grey);
	box-shadow: var(--box-shadow);
	z-index: 100;
	border: black solid 1px;

}

.settings-list-header {
	display: flex;
	justify-content: space-between;
	text-decoration: black underline 1px;
	font-size: small;
	margin-top: 2%;
}

.settings-list {
	display: flex;
	flex-direction: column;
}

.graph-operation,
.graph-operation-selected {
	display: flex;
	justify-content: space-between;
	width: 100%;
	padding: 1% 0 1% 0;
}

.operation-name-box {
	cursor: pointer;
}

.length-input {
	margin-left: 2rem;
	width: 3rem;
}

.graph-button-box {
    
	width: 100%;
    display: flex;
    justify-content:space-between;

    padding: 2% 3% 0 0;
}

.graph-button {
    width: max-content;
    padding: 1% 3% 1% 3%;
}
