.discourse-selector {
  background: var(--discourse-display-bg);

  display: grid;
  grid-template-columns: .3fr .6fr 1fr .6fr .3fr;
  grid-template-rows: 3fr 0.1fr 3fr;

  width: 100%;
  padding: 2%;
}

.matheme-selector,
.matheme-selector:focus{
  padding-left: 4%;
  background: none;
  border: none;

  -moz-appearance:none; /* Firefox */
  -webkit-appearance:none; /* Safari and Chrome */
  appearance:none;
  background: url(./../../../assets/selectarrow.svg) no-repeat 96% 95%;

  
  font-family: inherit;
  text-shadow: inherit;
  overflow: visible;
}

.matheme-selector:focus {

  outline-style: ridge;

}

.matheme-selector > option {
  background: var( --matheme-option-bg);
}