.discourse-display {
  background: var(--discourse-display-bg);

  display: grid;
  grid-template-columns: .3fr .6fr 1fr .6fr .3fr;
  grid-template-rows: 3fr .0001fr 3fr;

  width: 100%;
  padding: 2%;
  box-sizing: border-box;
}

.bar {
  max-width: 140%;
}

.bar-image-container {
  grid-row-start: 2;
  grid-row-end: 3;
  
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5%;
}

.bar-image-container:nth-of-type(1) {
  grid-column-start: 2;
  grid-column-end: 3;
}

.bar-image-container:nth-of-type(2) {
  grid-column-start: 4;
  grid-column-end: 5;
}

.impossibility {
  max-width: 100%;
  transform: translateY(0.6vh);
}

.impossibility-image-container {
  grid-column-start: 3;
  grid-column-end: 4;

  display: flex;
  align-items: center;
  justify-content: center;
}

.impotence {
  max-width: 60%;
}

.impotence-image-container {
  width: 100%;
  height: 100%;
  grid-column-start: 3;
  grid-column-end: 4;
  grid-row-start: 3;
  grid-row-end: 4;

  display: flex;
  justify-content: center;
  align-items: flex-end;
  margin-bottom: -8%;
}

.necessity-image-container {
  height: 70%;
  width: 100%;
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 4;

  display: flex;
  justify-content: flex-end;
  align-self: center;
  padding-right: 40%;
}
.contingency-image-container {
  height: 70%;
  width: 100%;
  grid-column-start: 5;
  grid-column-end: 6;
  grid-row-start: 1;
  grid-row-end: 4;

  display: flex;
  justify-content: flex-start;
  padding-left: 40%
}
