@import url(https://fonts.googleapis.com/css2?family=Trocchi&display=swap);
@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@500&family=JetBrains+Mono:wght@500&display=swap);
body {
	margin: 0;
	height: 100%;
}

html {
	height: 100%;
}

#root {
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;

	--main-font: "Trocchi", serif;
	--mono-font: "JetBrains Mono", monospace;

	/* default hsl(6, 46, 42) */
	--start-hue: 6;
	--saturation: 30%;
	--lightness: 40%;

	--primary-color1: hsl(var(--start-hue), var(--saturation), var(--lightness));
	--primary-color2: hsl(calc(var(--start-hue) + 90), var(--saturation), var(--lightness));
	--primary-color3: hsl(calc(var(--start-hue) + 270), var(--saturation), var(--lightness));
	--primary-color4: hsl(calc(var(--start-hue) + 180), var(--saturation), var(--lightness));
	--primary-color5: hsl(54,  var(--saturation), var(--lightness));

	/* --primary-color1: rgb(156, 67, 58);
	--primary-color2: rgb(98, 156, 58);
	--primary-color3: rgb(116, 58, 156);
	--primary-color4: rgb(58, 147, 156);
	--primary-color5: rgb(179, 168, 72); */

	--joker-color1: chartreuse;
	--joker-color2: tomato;
	--joker-color3: purple;

	--grey-lighter: rgb(230, 230, 230);
	--grey-light: rgb(225, 225, 225);
	--grey: rgb(211, 211, 211);
	--grey-dark: rgb(169, 169, 169);
	--grey-darker: rgb(128, 128, 128);
	--grey-reddish: rgb(138, 125, 125);
	--black: rgb(0, 0, 0);

	--header-color: rgb(174, 103, 91);
	--button-highlight: rgb(240, 230, 140); /* khaki */
	--button-effect: rgb(173, 255, 47); /* greenyellow */

	--bg-color: rgba(173, 167, 144, 0.7);
	--textbox-bg: var(--grey-light);
	--g-button-bg: var(--grey);
	--g-button-hover-bg: rgb(240, 230, 140);
	--clear-click-bg: rgb(173, 255, 47);

	--discourse-display-bg: var(--grey);

	--discourse-1-title-bg: var(--primary-color1);
	--discourse-2-title-bg: var(--primary-color2);
	--discourse-3-title-bg: var(--primary-color3);
	--discourse-4-title-bg: var(--primary-color4);
	--outer-ring-title-bg: var(--primary-color5);

	--agent: var(--primary-color1);
	--other: var(--primary-color2);
	--product: var(--primary-color3);
	--truth: var(--primary-color4);
	--matheme-option-bg: var(--grey-lighter);

	--nametable-bg: var(--grey-reddish);
	--nametable-title-bg: var(--header-color);
	--nametable-header-bg: var(--grey-dark);
	--new-discourse-option-bg: var(--grey-lighter);

	--overview-title-bg: var(--header-color);
	--overview-body-bg: var(--grey-dark);
	--ring-even-bg: var(--grey);
	--ring-odd-bg: var(--grey);
	--ring-title-text: var(--black);
	--ring-title-bg: var(--grey-dark);
	--ring-title-even-bg: var(--grey-dark);
	--in-ring-name-text: var(--black);

	--info-box-outer-bg: var(--grey);
	--info-box-inner-bg: var(--grey-light);

	--blaze-color: var(--joker-color1);
	--blaze-shadow: var(--joker-color2);
	--four20-color: var(--joker-color2);
	--four20-shadow: var(--joker-color3);

	--box-shadow: 1px 1px rgb(0, 0, 0, 0.4), 2px 2px rgb(0, 0, 0, 0.3),
		4px 4px rgba(0, 0, 0, 0.2);

	font-family: var(--main-font);
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

.image-container img {
	max-height: 100%;
	width: auto;
}

.matheme-selector {
  width: 100%;
  font-size: 300%;
  text-align-last:center;

}

.matheme-selector * {
  font-size: 100%;
  justify-self: center;

}

.matheme-selector:nth-of-type(1) {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 2;
  color: var(--agent);
}

.matheme-selector:nth-of-type(2) {
  grid-column-start: 4;
  grid-column-end: 5;
  grid-row-start: 1;
  grid-row-end: 2;
  color: var(--other);
}

.matheme-selector:nth-of-type(3) {
  grid-column-start: 4;
  grid-column-end: 5;
  grid-row-start: 3;
  grid-row-end: 4;
  color: var(--product);
}

.matheme-selector:nth-of-type(4) {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 3;
  grid-row-end: 4;
  color: var(--truth);
}


.discourse-selector {
  background: var(--discourse-display-bg);

  display: grid;
  grid-template-columns: .3fr .6fr 1fr .6fr .3fr;
  grid-template-rows: 3fr 0.1fr 3fr;

  width: 100%;
  padding: 2%;
}

.matheme-selector,
.matheme-selector:focus{
  padding-left: 4%;
  background: none;
  border: none; /* Firefox */
  -webkit-appearance:none; /* Safari and Chrome */
  appearance:none;
  background: url(/more-discourses/static/media/selectarrow.9c4bacc1.svg) no-repeat 96% 95%;

  
  font-family: inherit;
  text-shadow: inherit;
  overflow: visible;
}

.matheme-selector:focus {

  outline-style: ridge;

}

.matheme-selector > option {
  background: var( --matheme-option-bg);
}
.discourse-display {
  background: var(--discourse-display-bg);

  display: grid;
  grid-template-columns: .3fr .6fr 1fr .6fr .3fr;
  grid-template-rows: 3fr .0001fr 3fr;

  width: 100%;
  padding: 2%;
  box-sizing: border-box;
}

.bar {
  max-width: 140%;
}

.bar-image-container {
  grid-row-start: 2;
  grid-row-end: 3;
  
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5%;
}

.bar-image-container:nth-of-type(1) {
  grid-column-start: 2;
  grid-column-end: 3;
}

.bar-image-container:nth-of-type(2) {
  grid-column-start: 4;
  grid-column-end: 5;
}

.impossibility {
  max-width: 100%;
  -webkit-transform: translateY(0.6vh);
          transform: translateY(0.6vh);
}

.impossibility-image-container {
  grid-column-start: 3;
  grid-column-end: 4;

  display: flex;
  align-items: center;
  justify-content: center;
}

.impotence {
  max-width: 60%;
}

.impotence-image-container {
  width: 100%;
  height: 100%;
  grid-column-start: 3;
  grid-column-end: 4;
  grid-row-start: 3;
  grid-row-end: 4;

  display: flex;
  justify-content: center;
  align-items: flex-end;
  margin-bottom: -8%;
}

.necessity-image-container {
  height: 70%;
  width: 100%;
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 4;

  display: flex;
  justify-content: flex-end;
  align-self: center;
  padding-right: 40%;
}
.contingency-image-container {
  height: 70%;
  width: 100%;
  grid-column-start: 5;
  grid-column-end: 6;
  grid-row-start: 1;
  grid-row-end: 4;

  display: flex;
  justify-content: flex-start;
  padding-left: 40%
}

.matheme-display {
  font-size: 5vh;
  text-align:end;
  background: transparent;
  width: 100%;
  height: 100%;

  display: flex;
  justify-content: center;
}

.matheme-display {
  align-items: flex-end;
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 2;
  color: var(--agent);
  
}

.matheme-display:nth-of-type(2) {
  grid-column-start: 4;
  grid-column-end: 5;
  grid-row-start: 1;
  grid-row-end: 2;
  color: var(--other);
}

.matheme-display:nth-of-type(3) {
  align-items: flex-start;
  grid-column-start: 4;
  grid-column-end: 5;
  grid-row-start: 3;
  grid-row-end: 4;
  color: var(--product);
}

.matheme-display:nth-of-type(4) {
  align-items: flex-start;
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 3;
  grid-row-end: 4;
  color: var(--truth);
}




.ring-display {
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-template-rows: auto 1fr 1fr;
	margin: .5%;
	padding: 1%;

	height: 100%;
	width: 100%;

	background: var(--ring-odd-bg);
}

.ring-display:nth-of-type(2n) {
	background: var(--ring-even-bg);
}

.ring-title {
	border: 1px solid black;
	padding-left: 2%;
	grid-column-start: 1;
	grid-column-end: 3;
	grid-row-start: 1;
	grid-row-end: 2;


  color: var(--ring-title-text);
  background: var(--ring-title-bg);
  cursor: pointer;
}

.ring-display:nth-of-type(2n) > div:nth-child(1) {
  background: var(--ring-title-even-bg);
}

.in-ring-name {
	border: solid 1px black;
	cursor: pointer;
	display: flex;
	align-items: center;
	justify-content: center;
	text-align: center;

	min-width: -webkit-min-content;

	min-width: min-content;
	width: 100%;



	color: var(--in-ring-name-text);
	font-size: 85%;
}

.overview {
	position: relative;
	left: -720%;
	bottom: -118%;

	/* width: 1350%; */
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	min-width: 40vh;
	min-height: 34vh;

	margin: 0;
	/* resize: both;
	overflow: hidden; */
	display: grid;
	grid-template-rows: auto 1fr;
	grid-template-columns: 1fr;

	z-index: 1501;
	border: solid black 1px;
	box-shadow: var(--box-shadow);

	opacity: 0.98;
}

.overview-title {
	grid-column-start: 1;
	grid-column-end: 4;
	grid-row-start: 1;
	grid-row-end: 2;

	height: -webkit-fit-content;

	height: -moz-fit-content;

	height: fit-content;
	border: solid black 1px;
	background: var(--overview-title-bg);
	text-align: center;

	opacity: 1;

	cursor: grab;
}

.overview-body {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	grid-template-rows: 1fr 1fr;

	width: 37vw;
	height: 100%;

	background: var(--overview-body-bg);
}

.overview-title:active {
	cursor: grabbing;
}

.close-overview-button {
	position: absolute;
	right: 2px;
	top: 0;
}

.operations-box {
	grid-row-start: 3;
	grid-row-end: 4;
	grid-column-start: 1;
	grid-column-end: 2;
	z-index: 10;

	margin-left: 4%;
	margin-top: 5%;
	padding: 3%;
	padding-left: 1%;
	padding-top: 0;
	background-color: var(--grey);
	width: 12rem;
	height: 8rem;
	border: 1px solid black;

	box-shadow: var(--box-shadow);
}

.operations-list-title {
	margin: 1% 0 1% 0.5%;
}

.operations-list {
	border: black solid 1px;
	margin-left: 4%;
	width: 98%;
	padding: 1%;
}

.operation-name {
	cursor: pointer;
}

.outer-grid {
	display: grid;
	grid-template-rows: 18vh 2.5vh 18vh 18vh 18vh 2.5vh 18vh;
	grid-template-columns: auto 2.5vw 18vw 18vw 18vw 2.5vw auto;

	height: 100%;
	width: 100%;
	margin: 0.4%;

	position: relative;
}

.image-container {
	justify-self: center;
	align-self: center;
}

button,
.discourse-container,
.image-container {
	/* makes all buttons, discourses, images unselectable */
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

.generic-button {
	z-index: 1500;
	background: var(--g-button-bg);
	font-family: inherit;
	border: 1px solid black;
	box-shadow: 2px 2px rgb(63, 63, 63);
	overflow: hidden;
}

.generic-button:hover {
	background: var(--g-button-hover-bg);
}

.generic-button:active {
	-webkit-transform: translateY(2px);
	        transform: translateY(2px);
	box-shadow: 0px 0px rgb(63, 63, 63);
}

.text-button {
	border: none;
	background: none;
	font-size: inherit;
	font-family: var(--mono-font);
	cursor: pointer;
	z-index: 1;
}

.outer-ring-title,
.inner-ring-title {
	z-index: 500;
	opacity: 0.9;
	background-color: var(--grey-dark);
	justify-self: right;
	padding: 1%;
	margin-right: 5%;
	border: 1px solid black;

	width: 52%;
	min-width: -webkit-fit-content;
	min-width: -moz-fit-content;
	min-width: fit-content;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	min-height: -webkit-fit-content;
	min-height: -moz-fit-content;
	min-height: fit-content;

	font-size: 80%;
	cursor: pointer;
}

.outer-ring-title {
	grid-row-start: 1;
	grid-row-end: 2;
	grid-column-start: 3;
	grid-column-end: 4;

	align-self: flex-end;
}

.inner-ring-title {
	grid-row-start: 3;
	grid-row-end: 4;
	grid-column-start: 3;
	grid-column-end: 4;

	margin-top: -6%;
}

.controls {
	grid-row-start: 3;
	grid-row-end: 4;
	grid-column-start: 5;
	grid-column-end: 6;

	display: flex;
}

.ccw-button,
.cw-button {
	height: 15%;
	width: 9%;
	font-size: 120%;
	margin-left: 0.5%;

	font-weight: 1000;
}

.clear-button {
	height: 15%;
	width: 35%;
	margin-left: 0.5%;

	grid-row-start: 3;
	grid-row-end: 4;
	grid-column-start: 5;
	grid-column-end: 6;
}

.clear-button:active {
	background: var(--clear-click-bg);
}

.menu {
	position: absolute;
	display: flex;
	flex-direction: column;
	z-index: 1;

	padding-left: 1%;
}

.nametable-open,
.overview-open,
.graph-settings-open {
	width: -webkit-max-content;
	width: max-content;
	height: -webkit-max-content;
	height: max-content;
	justify-self: flex-start;

	font: inherit;

	font-size: 1.6vh;

	width: max-content;
	height: max-content;

	z-index: 1;

	border: none;
	background: transparent;
	text-decoration: underline;
	cursor: pointer;
}

.nametable-open:hover,
.overview-open:hover,
.graph-settings-open:hover {
	-webkit-text-decoration-style: double;
	        text-decoration-style: double;
	text-shadow: 0.8px 0.8px rgba(0, 0, 0, 0.1);
}

/* bunlar başka türlü addresslenir mi? */
.custom-operation {
	grid-row-start: 1;
	grid-row-end: 2;
	grid-column-start: 4;
	grid-column-end: 5;
	width: -webkit-min-content;
