	grid-column-start: 2;
	grid-column-end: 3;
	grid-row-start: 1;
	grid-row-end: 2;

	background: var(--discourse-1-title-bg);
	color: white;
	text-shadow: 1px 1px black;
	justify-self: center;
	border: 1px solid black;
	box-shadow: var(--box-shadow);
	/* box-shadow: 2px 2px 5px black; */
}

.discourse-container:nth-of-type(2) {
	grid-column-start: 3;
	grid-column-end: 4;
	grid-row-start: 2;
	grid-row-end: 3;
	background: var(--discourse-2-title-bg);
	justify-self: left;
}

.discourse-container:nth-of-type(3) {
	grid-column-start: 2;
	grid-column-end: 3;
	grid-row-start: 3;
	grid-row-end: 4;
	background: var(--discourse-3-title-bg);
}

.discourse-container:nth-of-type(4) {
	grid-column-start: 1;
	grid-column-end: 2;
	grid-row-start: 2;
	grid-row-end: 3;
	background: var(--discourse-4-title-bg);
	justify-self: right;
}

.discourse-header {
	position: relative;
	height: 12%;
	border-bottom: 1px solid black;
	display: flex;
	justify-content: center;
}

.name-display {
	font-size: 100%;
}

.quick-add-button {
	visibility: hidden;
	height: 100%;
	position: absolute;
	right: 0.4%;
	top: 0.2%;
	display: flex;
	justify-content: center;
	align-items: center;
