.operations-box {
	grid-row-start: 3;
	grid-row-end: 4;
	grid-column-start: 1;
	grid-column-end: 2;
	z-index: 10;

	margin-left: 4%;
	margin-top: 5%;
	padding: 3%;
	padding-left: 1%;
	padding-top: 0;
	background-color: var(--grey);
	width: 12rem;
	height: 8rem;
	border: 1px solid black;

	box-shadow: var(--box-shadow);
}

.operations-list-title {
	margin: 1% 0 1% 0.5%;
}

.operations-list {
	border: black solid 1px;
	margin-left: 4%;
	width: 98%;
	padding: 1%;
}

.operation-name {
	cursor: pointer;
}
