.main-container {
    position: relative;
    height: 100vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 1%;
    border: 2px solid black;

    background: url(../../assets/ouroboros.svg), url(../../assets/radial.svg);
    background-color: var(--bg-color);
    background-size: contain, 43%;
    background-position: center;
    background-repeat: no-repeat;
    background-blend-mode: overlay, screen;
    /* backgound image'ı transparan yapılmıyor 
    kendim editleyerek yaptım */
  }

  .blaze-nodisplay {
    display: none;
  }

  .blaze-display  {
    display: grid;
    grid-template-rows: 1fr 0 1fr;
    grid-template-columns: 1fr;
    position: absolute;

    text-align: center;
    font-size: 20vh;
    color: var(--blaze-color);
    text-shadow: 1vh 1vh 0 var(--blaze-shadow);
    text-decoration: wavy black;
    z-index: 1600;


    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

  }

  .blaze-display p:nth-of-type(1) {
    font-size: 35vh;
    color: var(--four20-color);
    text-shadow: 1vh 1vh 0 var(--four20-shadow);

    animation: swing 600ms infinite linear;
  }

  .blaze-display p:nth-of-type(2) {

    animation: pulse 150ms linear infinite alternate;

  }

  @keyframes pulse {
    from {
      transform: scale(1);
    }
    to {
      transform: scale(1.5) translateY(-20%);
    }
  }

  @keyframes swing {
    0% {
      transform: rotate(0deg);
    }
    25% {
      transform: rotate(-30deg)  translateX(10%);
    }
    75%  {
      transform: rotate(30deg) translateX(-10%);
    }
    100% {
      transform: rotate(0deg);
    }
  } 