.ring-display {
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-template-rows: auto 1fr 1fr;
	margin: .5%;
	padding: 1%;

	height: 100%;
	width: 100%;

	background: var(--ring-odd-bg);
}

.ring-display:nth-of-type(2n) {
	background: var(--ring-even-bg);
}

.ring-title {
	border: 1px solid black;
	padding-left: 2%;
	grid-column-start: 1;
	grid-column-end: 3;
	grid-row-start: 1;
	grid-row-end: 2;


  color: var(--ring-title-text);
  background: var(--ring-title-bg);
  cursor: pointer;
}

.ring-display:nth-of-type(2n) > div:nth-child(1) {
  background: var(--ring-title-even-bg);
}

.in-ring-name {
	border: solid 1px black;
	cursor: pointer;
	display: flex;
	align-items: center;
	justify-content: center;
	text-align: center;

	min-width: min-content;
	width: 100%;



	color: var(--in-ring-name-text);
	font-size: 85%;
}
