.input-form {
  margin: 0;
  width: 100%;
  

  grid-column-start: 1;
  grid-column-end: 7;


  display: grid;
  grid-template-columns: 40fr 10fr 10fr 10fr 10fr auto;
  /* justify-content: space-between; */
  /* that does nothing  */
}

.discourse-input {
    border: none;
    border-right: solid 1px black;
  border-bottom: solid 1px black;
  background: var(--textbox-bg);
  width: 100%;
  font-family: inherit;
  text-indent: 2%;
}

.new-discourse-selector {
  width: 100%;
  border-right: 1px solid black;
  border-bottom: 1px solid black;
  font-family: inherit;
  font-size: inherit;
  color: black;
  background: var(--new-discourse-option-bg);
  text-align-last:center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.add-button {
  padding: 0 2px;
}
