.matheme-display {
  font-size: 5vh;
  text-align:end;
  background: transparent;
  width: 100%;
  height: 100%;

  display: flex;
  justify-content: center;
}

.matheme-display {
  align-items: flex-end;
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 2;
  color: var(--agent);
  
}

.matheme-display:nth-of-type(2) {
  grid-column-start: 4;
  grid-column-end: 5;
  grid-row-start: 1;
  grid-row-end: 2;
  color: var(--other);
}

.matheme-display:nth-of-type(3) {
  align-items: flex-start;
  grid-column-start: 4;
  grid-column-end: 5;
  grid-row-start: 3;
  grid-row-end: 4;
  color: var(--product);
}

.matheme-display:nth-of-type(4) {
  align-items: flex-start;
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 3;
  grid-row-end: 4;
  color: var(--truth);
}


