.name-table {
  position: absolute;
  left: 10vw;
  top: 1.5vh;
  background: var(--nametable-bg);
  opacity: .98;
  border: solid black 1px;
  box-shadow: var(--box-shadow);

  /* height ve width silinebilir de  */
  /* height: 36vh; */
  width: 27vw;
  min-width: 20vw;
  min-height: 20vh;
  max-height: 95%;
  max-width: 95%;

  display: grid;
  grid-template-rows: max-content max-content auto;
  
  
  resize: both;
  overflow: auto;

  z-index: 1501;
}

.name-table-title {
  height: max-content;
  cursor: grab;
  /* display: flex;
    justify-content: space-between; */
  position: relative;
  
  background: var(--nametable-title-bg);
  
  opacity: 1;
  border-bottom: solid black 1px;
  width: 100%;
  padding: 0 0.4vw;
}

.name-table-title:active {
  cursor: grabbing;
}

.close-button {
  position: absolute;
  right: 0;
  top: 0;
}

.name-table-container {
  margin: 1%;
  border: black 1px solid;
  display: grid;
  grid-template-columns: 40fr 10fr 10fr 10fr 10fr auto;
  grid-template-rows: auto;

  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.name-table-container > span:nth-of-type(-n + 6) {
  border-bottom: dashed 1px black;

  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  background: var(--nametable-header-bg);
  text-align: center;
}


.table-footer {
  width: fit-content;
  height: fit-content;
  align-self: flex-end;
  justify-self: flex-end;
  padding: .8%;
  margin-right: 1%;
  margin-bottom: 1%;

  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: 1fr 1fr 1fr;

  border: 1px solid black;
}

.table-footer > button {
  width: 100%;
  height: 100%;
  padding-left: 10%;
  padding-right: 10%;
  display: flex;
  align-items: center;
  justify-content: center;
  }

.textbox {
  height: 8vh;
  width: 15vw;
  margin-right: none;
  padding: 1%;

  overflow: auto;
  resize: none;

  grid-row-start: 1;
  grid-row-end: 4;
  grid-column-start: 1;
  grid-column-end: 2;
}
