.overview {
	position: relative;
	left: -720%;
	bottom: -118%;

	/* width: 1350%; */
	width: fit-content;
	height: fit-content;
	min-width: 40vh;
	min-height: 34vh;

	margin: 0;
	/* resize: both;
	overflow: hidden; */
	display: grid;
	grid-template-rows: auto 1fr;
	grid-template-columns: 1fr;

	z-index: 1501;
	border: solid black 1px;
	box-shadow: var(--box-shadow);

	opacity: 0.98;
}

.overview-title {
	grid-column-start: 1;
	grid-column-end: 4;
	grid-row-start: 1;
	grid-row-end: 2;

	height: fit-content;
	border: solid black 1px;
	background: var(--overview-title-bg);
	text-align: center;

	opacity: 1;

	cursor: grab;
}

.overview-body {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	grid-template-rows: 1fr 1fr;

	width: 37vw;
	height: 100%;

	background: var(--overview-body-bg);
}

.overview-title:active {
	cursor: grabbing;
}

.close-overview-button {
	position: absolute;
	right: 2px;
	top: 0;
}
