.matheme-selector {
  width: 100%;
  font-size: 300%;
  text-align-last:center;

}

.matheme-selector * {
  font-size: 100%;
  justify-self: center;

}

.matheme-selector:nth-of-type(1) {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 2;
  color: var(--agent);
}

.matheme-selector:nth-of-type(2) {
  grid-column-start: 4;
  grid-column-end: 5;
  grid-row-start: 1;
  grid-row-end: 2;
  color: var(--other);
}

.matheme-selector:nth-of-type(3) {
  grid-column-start: 4;
  grid-column-end: 5;
  grid-row-start: 3;
  grid-row-end: 4;
  color: var(--product);
}

.matheme-selector:nth-of-type(4) {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 3;
  grid-row-end: 4;
  color: var(--truth);
}
